import Vue from 'vue'
import Vuex from 'vuex'
import crudvuexMod  from '@/store/CRUDvuex';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    productPage: 1, 

    // (TODO) Feed this product array via REST APIs. Try use faker.js if possible
    products: [
        
        // TYPES: 
        //      'product':'CPU i9','manufacturer':'Intel','supplier':'MercadoLivre','category':'computer,electronics',selectedCategory: ''
        //       product:String, manufacturer: String, supplier: String, category: String, selectedCategory: String;
        
        {  'id': '1', 'product': 'CPU i9', 'manufacturer': 'Intel', 'supplier': 'MercadoLivre', 'category': 'computer,electronics', selectedCategory: ''},
        {  'id': '2','product': 'CPU i7', 'manufacturer': 'Intel', 'supplier': 'OLX', 'category': 'computer,electronics', selectedCategory: ''},
        {  'id': '3','product': 'CPU i5', 'manufacturer': 'Intel', 'supplier': 'Amazon', 'category': 'computer,electronics', selectedCategory: ''},
        {  'id': '4','product': 'CPU i3', 'manufacturer': 'Intel', 'supplier': 'Amazon', 'category': 'computer,electronics', selectedCategory: ''},
        {  'id': '5','product': 'CPU Pentium Pro ', 'manufacturer': 'Intel', 'supplier': 'MercadoLivre', 'category': 'computer,electronics', selectedCategory: ''},
        {  'id': '6','product': 'CPU i5', 'manufacturer': 'Intel', 'supplier': 'MercadoLivre', 'category': 'computer,electronics', selectedCategory: ''},
        {  'id': '7','product': 'CPU PII', 'manufacturer': 'Intel', 'supplier': 'Ebay', 'category': 'computer,electronics', selectedCategory: ''},
        {  'id': '8','product': 'CPU PIII', 'manufacturer': 'Intel', 'supplier': 'Ebay', 'category': 'computer,electronics', selectedCategory: ''},        
        {  'id': '9','product': 'CPU PIV', 'manufacturer': 'Intel', 'supplier': 'Ebay', 'category': 'computer,electronics', selectedCategory: ''},
        {  'id': '10','product': 'CPU Pentium 133 MMX', 'manufacturer': 'Intel', 'supplier': 'Ebay', 'category': 'computer,electronics', selectedCategory: ''},
      ],     
  },
  getters: {
    getAllProducts(state) {
      return state.products
    },
    getAllProductsCount(state) {      
      return state.products.length
    }

  },
  mutations: {
    // SET_PRODUCTS(state, products) {
    //   state.products = products;
    // },
    ADD_PRODUCT(state, product) {
    
      const productExist = state.products.find( p => 
         ( p.id === product.id ||  p.product.toLocaleLowerCase() === product.product.toLocaleLowerCase() ))
      if (productExist) {
        console.log('ADD_PRODUCT mutation: Product alreadly exists');
        return        
      }
      state.products.push(product);      
    },

    UPDATE_PRODUCT(state, { index, updatedProduct }) {
      state.products[index] = updatedProduct;
    },
    REMOVE_PRODUCT(state, index) {
      state.products.splice(index, 1);
    },    

  },
  actions: {
    // setProducts({ commit }, products) {
    //   commit('SET_PRODUCTS', products);
    // },
    addProduct({ commit }, product) {
      commit('ADD_PRODUCT', product);
    },
    updateProduct({ commit }, { index, updatedProduct }) {
      commit('UPDATE_PRODUCT', { index, updatedProduct });
    },
    removeProduct({ commit }, index) {
      commit('REMOVE_PRODUCT', index);
    },    
  },
  modules: {
    crudvuexMod
  }
})
