import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Cadprecos from '../components/Cadprecos.vue'
import PesquisaProdutos from '../components/PesquisaProdutos.vue'
import CrudVuex from '../components/CRUDvuex.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/crudvuex',
    name: 'crudvuex',
    component: CrudVuex
  },  
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/cadprecos',
    name: 'cadeprecos',
    component: Cadprecos
  },
  {
    path: '/pesquisaprodutos',
    name: 'pesquisaprodutos',
    component: PesquisaProdutos
  },  

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
