<template>
  <div class="hello">   
    <b-container class="bv-example-row">
      <b-row>
        <b-col cols="12" class="">                      
          <b-form @submit.stop.prevent class="d-flex border w-100"> 
            <div class="w-100">
              <label for="productName">Pesquisar Nome,Fabricante, Fornecedor, Categoria</label>   <br>
              <div class="d-inline-flex w-100" >
                <b-form-input   
                          class="w-100"
                          v-model="productName" 
                          placeholder="...">
                </b-form-input>    
                <div class="d-flex flex-row align-items-center justify-content-center pl-2">
                  <b-btn                          
                          name="newProduct"  
                          @click="toggleTransition()"                     
                          variant="primary">+ 
                  </b-btn>                        
                </div>                    
              </div>
            </div> 
            <div class="border w-100" >
              <transition name="slide-left" class="border buttons" >
                    <div v-if="buttonsShow" class="d-flex flex-row align-items-center justify-content-between h-100 w-25">
                      <span>A </span>
                      <span>B </span>
                      <span>C </span>                    
                    </div>
              </transition>                  
            </div>
          </b-form>     
        </b-col>        
      </b-row>    
      <b-col class="mt-5">
        <div>               
          <b-table 
                  @row-clicked="onRowClicked" 
                  :fields="fields"
                  :items="filteredData"                                     
                  >
          <template  v-slot:cell()="data">
            <span :class="{ 'text-green': data.item.isFiltered, 'text-red': data.item.isFiltered }">            
              <i>{{ data.value }}</i>
            </span>                        
          </template>
          <template #cell(product)="data">
            <span :class="{ 'text-green': data.item.isFiltered, 'text-red': data.item.isFiltered }">
              <b class="text-info">
                {{ data.item.product }}
              </b>            
            </span>            
          </template>
          <template #cell(category)="data">
            <span :class="{ 'text-green': data.item.isFiltered }">               
                <div v-for="(i, index) in categories(data.item.category)" :key="index">
                  <span :class="{ 'text-green': data.item.isFiltered, 'text-red': data.item.isFiltered }">   
                    <span style="color: #409aef; cursor: pointer" @click="log(i)">#{{i}}</span>
                    </span>
                </div>                
                <!-- <span v-html="hashfy(data.item.category)"></span> -->
            </span>                                  
          </template>          
          <template #cell(productmanufacturer)="data">
            <span :class="{ 'text-green': data.item.isFiltered }">
                {{ data.item.product}} is manufactured by {{ data.item.manufacturer }}             
            </span>                                  
          </template>                    
          </b-table>
        </div>        
      </b-col>    
      
    </b-container>
  </div>  
</template>

<script>
import { mapGetters, mapActions, mapState } from 'vuex'

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      turnOn: false, // to turn on
      productName: '',
      productManufacturer: '',
      productCategory: '',  
      fields: [ {key: 'product', label: 'Product'}, 
                {key: 'manufacturer', label: 'Manufacturer'}, 
                {key: 'supplier', label: 'Supplier'}, 
                {key: 'category', label: 'Category'}, 
                {key: 'productmanufacturer' , label: 'Product and manufacturer'} ],   

      itemSelected: '',
      buttonsShow: false,
    }
  },
  mounted: function() {
    console.log('mounted')
  },
  methods: {

    ...mapActions( [
          // 'setProducts',
          'addProduct',
          'updateProduct',
          'removeProduct',
    ]),    
    
    calle() {

      // Getter getAllProductsCount user for add next id
      // See more ways for manually generate those ids
      const count = this.getAllProductsCount
      console.log(count)
      
      const obj = { 
                    'id': count+1 , 
                    'product': '486', 
                    'manufacturer': 'Intel', 
                    'supplier': 'Amazon', 
                    'category': 'computers, electronics',
                    'selectedCategory': '' 


                  }            

      this.handleAddProduct(obj)
    },
    handleAddProduct(newProduct) {

      this.addProduct(newProduct);
    },
    handleUpdateProduct(index, updatedProduct) {
      this.updateProduct({ index, updatedProduct });
    },
    handleRemoveProduct(index) {
      this.removeProduct(index);
    },  
    toggleTransition() {

      console.log('Calling called()..')
      this.calle();

      // Calling the addproduct action            
      this.buttonsShow = !this.buttonsShow
    },
    categories(rowCats) {
      
      return rowCats.split(',')

    },
    onRowClicked(item, index, event) {
      // aqui é o array destructuring separando apenas      
      const { product, manufacturer, supplier } = item
      
      // Change the isFiltered property to true when clicked
      // Changes who? Changes current item via vuex actions.
      // How? 

      //Logging current product on console 
      console.log(index + ":" + product + ',' + manufacturer + ',' + supplier); 

      event

    },
    log(el) {
      console.log(el)
    },
    hashfy(csv) {
      var list = csv.split(',')  
      var hashy = list.map(ele => {        
        // style="cursor: pointer; border-radius: 5px; margin: 2px; padding: 3px; font-size: 1em; color: #409aef; "
        return `<button onclick="" style="font-size: .8em; color: #409aef; cursor:pointer">#${ele} </button>`
      })
      
      return hashy.join('')

    },
    
  },
  computed: {


    ...mapState('crudvuex', ['data'] ),

    // filteredData() {     
    //   const searchTerm = this.productName.toLowerCase();      
    //   return this.products.filter(row =>        
    //     // Se retornar true o filtro adiciona o elemento ao 
    //     // array retornado. Com Object.values retorna todos
    //     // os valores das keys existentes. Usando 
    //     // Object.values(row).some() verifica-se algum retorna
    //     // true para a condicicao = value.toLowerCase().includes(searchForm
    //     Object.values(row).some(value => {                      
    //         value.toLowerCase().includes(searchTerm)
    //       }
    //     )
    //   )
    // },

    ...mapGetters([
      'getAllProducts',
      'getAllProductsCount'
    ]),
    getProductsCount() {
      return this.getAllProductsCount;
    },
    filteredData() {      

      // BUG: autocomplete do VSCode não reconhece mappedGetters
      // console.log(this.getAllProducts)     
      
      const searchTerm = this.productName.toLowerCase();      
      var filteredItems =  this.getAllProducts.map( row => {           
        const isFiltered = searchTerm ?               
              Object.values(row).some(value => 
                value.toLocaleLowerCase().includes(searchTerm)
              )
              : false;        
        return { ...row, isFiltered }        
      })
      // 
      // Adding (ASC) sorting feature. 
      //
      filteredItems.sort((a, b) => {
        const aIndex = a.isFiltered
        const bIndex = b.isFiltered
        // DESC: return aIndex - bIndex
        return bIndex - aIndex;
      });
      return filteredItems;           
      //else if (groupby_filter) {
      //}
    },    
  }
}
</script>

